import { EVT } from './EVT';
import { lang } from './Lang';
// 
class ShowSkipCount {
    constructor(el) {
        this.count = 0; // 
        this.el = el;
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.reset();
        });
        window.addEventListener(EVT.list.downloadStop, () => {
            // 
            this.count = 0;
        });
        window.addEventListener(EVT.list.skipDownload, () => {
            this.addCount();
        });
        window.addEventListener(EVT.list.downloadStart, () => {
            if (this.count === 0) {
                this.reset();
            }
        });
        window.addEventListener(EVT.list.downloadComplete, () => {
            // 
            this.count = 0;
        });
    }
    addCount() {
        this.count++;
        this.el.textContent = lang.transl('_n', this.count.toString());
    }
    reset() {
        this.count = 0;
        this.el.textContent = '';
    }
}
export { ShowSkipCount };
