// 
import { EVT } from './EVT';
import { DOM } from './DOM';
import { store } from './Store';
import { log } from './Log';
import { lang } from './Lang';
import { Colors } from './Colors';
import { settings } from './setting/Settings';
import { Download } from './Download';
import { progressBar } from './ProgressBar';
import { downloadStates } from './DownloadStates';
import { ShowSkipCount } from './ShowSkipCount';
import { ShowConvertCount } from './ShowConvertCount';
import { BookmarkAfterDL } from './BookmarkAfterDL';
import { resume } from './Resume';
import { states } from './States';
import Config from './Config';
class DownloadControl {
    constructor() {
        this.thread = 5; // 
        //  5
        //  Config.downloadThreadMax 
        this.taskBatch = 0; // 
        this.taskList = {}; //  id  key
        this.errorIdList = []; //  id
        this.downloaded = 0; // 
        this.wrapper = document.createElement('div');
        this.totalNumberEl = document.createElement('span');
        this.statesEl = document.createElement('span');
        this.stop = false; // 
        this.pause = false; // 
        this.createDownloadArea();
        this.listenEvents();
        const skipTipWrap = this.wrapper.querySelector('.skip_tip');
        new ShowSkipCount(skipTipWrap);
        const convertTipWrap = this.wrapper.querySelector('.convert_tip');
        new ShowConvertCount(convertTipWrap);
        //  p  pixivision
        if (location.hostname.endsWith('.pixiv.net')) {
            const bmkAfterDLTipWrap = this.wrapper.querySelector('.bmkAfterDL_tip');
            new BookmarkAfterDL(bmkAfterDLTipWrap);
        }
    }
    listenEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.hideDownloadArea();
            this.reset();
        });
        for (const ev of [
            EVT.list.crawlFinish,
            EVT.list.resultChange,
            EVT.list.resume,
        ]) {
            window.addEventListener(ev, () => {
                window.setTimeout(() => {
                    this.readyDownload();
                }, 0);
            });
        }
        window.addEventListener(EVT.list.skipDownload, (ev) => {
            const data = ev.detail.data;
            this.downloadOrSkipAFile(data);
        });
        window.addEventListener(EVT.list.downloadError, (ev) => {
            const id = ev.detail.data;
            this.downloadError(id);
        });
        // 
        chrome.runtime.onMessage.addListener((msg) => {
            if (!this.taskBatch) {
                return;
            }
            // 
            if (msg.msg === 'downloaded') {
                //  BLOBURL
                URL.revokeObjectURL(msg.data.url);
                // 
                EVT.fire(EVT.list.downloadSuccess, msg.data);
                this.downloadOrSkipAFile(msg.data);
            }
            else if (msg.msg === 'download_err') {
                // 
                log.error(`${msg.data.id} download error! code: ${msg.err}. The downloader will try to download the file again `);
                EVT.fire(EVT.list.saveFileError);
                // 
                // 
                this.saveFileError(msg.data);
            }
            // UUID 
            if (msg.data && msg.data.uuid) {
                log.error(lang.transl('_uuid'));
            }
        });
    }
    createDownloadArea() {
        const html = `<div class="download_area">
    <p> ${lang.transl('_n', '<span class="fwb blue imgNum">0</span>')}</p>
    
    <div class="centerWrap_btns">
    <button class="startDownload" type="button" style="background:${Colors.blue};"> ${lang.transl('_1')}</button>
    <button class="pauseDownload" type="button" style="background:${Colors.yellow};"> ${lang.transl('_2')}</button>
    <button class="stopDownload" type="button" style="background:${Colors.red};"> ${lang.transl('_3')}</button>
    <button class="copyUrl" type="button" style="background:${Colors.green};"> ${lang.transl('_url')}</button>
    </div>
    <div class="download_status_text_wrap">
    <span>${lang.transl('_')}</span>
    <span class="down_status">${lang.transl('_')}</span>
    <span class="skip_tip warn"></span>
    <span class="convert_tip warn"></span>
    <span class="bmkAfterDL_tip green"></span>
    </div>
    </div>`;
        this.wrapper = DOM.useSlot('downloadArea', html);
        this.statesEl = this.wrapper.querySelector('.down_status');
        this.totalNumberEl = this.wrapper.querySelector('.imgNum');
        this.wrapper
            .querySelector('.startDownload')
            .addEventListener('click', () => {
            this.startDownload();
        });
        this.wrapper
            .querySelector('.pauseDownload')
            .addEventListener('click', () => {
            this.pauseDownload();
        });
        this.wrapper
            .querySelector('.stopDownload')
            .addEventListener('click', () => {
            this.stopDownload();
        });
        this.wrapper.querySelector('.copyUrl').addEventListener('click', () => {
            EVT.fire(EVT.list.showURLs);
        });
    }
    // 
    readyDownload() {
        if (states.busy) {
            return;
        }
        if (store.result.length === 0) {
            return progressBar.reset(0);
        }
        this.showDownloadArea();
        this.totalNumberEl.textContent = store.result.length.toString();
        this.setDownloaded();
        this.setDownloadThread();
        //   
        if (states.notAutoDownload) {
            return;
        }
        // 
        if (settings.quietDownload || states.quickDownload) {
            this.startDownload();
        }
    }
    // 
    startDownload() {
        if (!this.pause && !resume.flag) {
            // 
            // 
            downloadStates.init();
        }
        else {
            // 
            // 
            downloadStates.resume();
        }
        this.reset();
        this.setDownloaded();
        this.taskBatch = new Date().getTime(); // 
        this.setDownloadThread();
        EVT.fire(EVT.list.downloadStart);
        // 
        for (let i = 0; i < this.thread; i++) {
            this.createDownload(i);
        }
        this.setDownStateText(lang.transl('_'));
        log.success(lang.transl('_'));
    }
    // 
    pauseDownload() {
        if (store.result.length === 0) {
            return;
        }
        // 
        if (this.stop === true) {
            return;
        }
        if (this.pause === false) {
            // 
            if (states.busy) {
                this.pause = true;
                this.setDownStateText(lang.transl('_'), '#f00');
                log.warning(lang.transl('_'), 2);
                EVT.fire(EVT.list.downloadPause);
            }
            else {
                // 
                return;
            }
        }
    }
    // 
    stopDownload() {
        if (store.result.length === 0 || this.stop) {
            return;
        }
        this.stop = true;
        this.setDownStateText(lang.transl('_'), '#f00');
        log.error(lang.transl('_'), 2);
        this.pause = false;
        EVT.fire(EVT.list.downloadStop);
    }
    downloadError(id) {
        this.errorIdList.push(id);
        // 
        const task = this.taskList[id];
        const no = task.progressBarIndex;
        if (this.checkContinueDownload()) {
            this.createDownload(no);
        }
    }
    saveFileError(data) {
        if (this.pause || this.stop) {
            return false;
        }
        const task = this.taskList[data.id];
        // 
        downloadStates.setState(task.index, -1);
        // 
        this.createDownload(task.progressBarIndex);
    }
    downloadOrSkipAFile(data) {
        const task = this.taskList[data.id];
        // 
        downloadStates.setState(task.index, 1);
        // 
        this.setDownloaded();
        // 
        const no = task.progressBarIndex;
        if (this.checkContinueDownload()) {
            this.createDownload(no);
        }
    }
    // 
    checkContinueDownload() {
        // 
        if (this.downloaded < store.result.length) {
            // 
            if (this.pause || this.stop) {
                return false;
            }
            //   
            if (this.downloaded + this.thread - 1 < store.result.length) {
                return true;
            }
            else {
                return false;
            }
        }
        else {
            return false;
        }
    }
    // 
    setDownloadThread() {
        const setThread = settings.downloadThread;
        if (setThread < 1 ||
            setThread > Config.downloadThreadMax ||
            isNaN(setThread)) {
            // 
            this.thread = Config.downloadThreadMax;
        }
        else {
            this.thread = setThread; // 
        }
        // 
        if (store.result.length - this.downloaded < this.thread) {
            this.thread = store.result.length - this.downloaded;
        }
        // 
        progressBar.reset(this.thread, this.downloaded);
    }
    // 
    createDownload(progressBarIndex) {
        const index = downloadStates.getFirstDownloadItem();
        if (index === undefined) {
            // 
            // 
            return this.checkCompleteWithError();
        }
        else {
            const workData = store.result[index];
            const data = {
                id: workData.id,
                data: workData,
                index: index,
                progressBarIndex: progressBarIndex,
                taskBatch: this.taskBatch,
            };
            // 
            this.taskList[workData.id] = {
                index,
                progressBarIndex: progressBarIndex,
            };
            // 
            new Download(progressBarIndex, data);
        }
    }
    setDownloaded() {
        this.downloaded = downloadStates.downloadedCount();
        const text = `${this.downloaded} / ${store.result.length}`;
        log.log(text, 2, false);
        // 
        progressBar.setTotalProgress(this.downloaded);
        if (this.downloaded === 0) {
            this.setDownStateText(lang.transl('_'));
        }
        // 
        if (this.downloaded === store.result.length) {
            window.setTimeout(() => {
                // 
                EVT.fire(EVT.list.downloadComplete);
            }, 0);
            this.reset();
            this.setDownStateText(lang.transl('_'), Colors.green);
            log.success(lang.transl('_'), 2);
        }
        this.checkCompleteWithError();
    }
    // 
    checkCompleteWithError() {
        if (this.errorIdList.length > 0 &&
            this.downloaded + this.errorIdList.length === store.result.length) {
            // 
            this.pauseDownload();
            setTimeout(() => {
                this.startDownload();
            }, 3000);
        }
    }
    // 
    setDownStateText(text, color = Colors.blue) {
        this.statesEl.textContent = text;
        this.statesEl.style.color = color;
    }
    reset() {
        this.pause = false;
        this.stop = false;
        this.errorIdList = [];
    }
    showDownloadArea() {
        this.wrapper.style.display = 'block';
    }
    hideDownloadArea() {
        this.wrapper.style.display = 'none';
    }
}
new DownloadControl();
