// 
import { InitPageBase } from './InitPageBase';
import { Colors } from './Colors';
import { lang } from './Lang';
import { options } from './setting/Options';
import { API } from './API';
import { store } from './Store';
import { EVT } from './EVT';
import { log } from './Log';
import { DOM } from './DOM';
import { states } from './States';
import './SaveAvatarIcon';
import './SaveAvatarImage';
import './SaveUserCover';
import { BookmarkAllWorks } from './BookmarkAllWorks';
import { Tools } from './Tools';
class InitUserPage extends InitPageBase {
    constructor() {
        super();
        this.listType = 0; // 
        this.onceNumber = 48; //  48  24 
        this.bookmarkAll = new BookmarkAllWorks();
        this.sendBookmarkIdList = () => {
            if (states.bookmarkMode) {
                //  id  type  illusts  novels
                const list = [];
                for (const data of store.idList) {
                    if (data.type === 'novels') {
                        list.push(data);
                    }
                    else {
                        list.push({
                            type: 'illusts',
                            id: data.id,
                        });
                    }
                }
                store.idList = []; //  id 
                this.bookmarkAll.sendIdList(list);
            }
        };
        this.init();
    }
    // 
    addCrawlBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    addAnyElement() {
        DOM.addBtn('otherBtns', Colors.green, lang.transl('_')).addEventListener('click', () => {
            EVT.fire(EVT.list.saveAvatarImage);
        });
        DOM.addBtn('otherBtns', Colors.green, lang.transl('_')).addEventListener('click', () => {
            EVT.fire(EVT.list.saveUserCover);
        });
        DOM.addBtn('otherBtns', Colors.green, lang.transl('_'), [
            ['title', lang.transl('_')],
        ]).addEventListener('click', () => {
            EVT.fire(EVT.list.saveAvatarIcon);
        });
        // 
        const bookmarkAllBtn = DOM.addBtn('otherBtns', Colors.green, lang.transl('_'));
        this.bookmarkAll = new BookmarkAllWorks(bookmarkAllBtn);
        bookmarkAllBtn.addEventListener('click', async () => {
            //  id 
            //  id 
            EVT.fire(EVT.list.bookmarkModeStart);
            store.tag = API.getTagFromURL();
            this.crawlNumber = 1; //  1 
            this.readyGetIdList();
        });
        window.addEventListener(EVT.list.getIdListFinished, this.sendBookmarkIdList);
    }
    setFormOption() {
        // /
        options.setWantPageTip({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: lang.transl('_1'),
        });
    }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), lang.transl('_'));
    }
    nextStep() {
        this.readyGetIdList();
        log.log(lang.transl('_'));
    }
    readyGetIdList() {
        // 
        //  pathname  id 
        const test = location.pathname.match(/\/users\/\d+(\/.+)/);
        if (test === null) {
            // 
            this.listType = 0;
        }
        else if (test.length === 2) {
            const str = test[1]; // id 
            if (str.includes('/artworks')) {
                // 
                this.listType = 1;
            }
            else if (str.includes('/illustrations')) {
                // 
                this.listType = 2;
            }
            else if (str.includes('/manga')) {
                // 
                this.listType = 3;
            }
            else if (str.includes('/novels')) {
                // 
                this.listType = 4;
                this.onceNumber = 24; //  24 
            }
        }
        store.tag ? this.getIdListByTag() : this.getIdList();
    }
    getOffset() {
        const nowPage = API.getURLSearchField(location.href, 'p'); //  1 
        let offset = 0;
        if (nowPage) {
            offset = (parseInt(nowPage) - 1) * this.onceNumber;
        }
        if (offset < 0) {
            offset = 0;
        }
        return offset;
    }
    // 
    getRequsetNumber() {
        let requsetNumber = 9999999;
        if (this.crawlNumber !== -1) {
            requsetNumber = this.onceNumber * this.crawlNumber;
        }
        return requsetNumber;
    }
    //  id 
    async getIdList() {
        let type = [];
        switch (this.listType) {
            case 0:
                type = ['illusts', 'manga', 'novels'];
                break;
            case 1:
                type = ['illusts', 'manga'];
                break;
            case 2:
                type = ['illusts'];
                break;
            case 3:
                type = ['manga'];
                break;
            case 4:
                type = ['novels'];
                break;
        }
        let idList = await API.getUserWorksByType(DOM.getUserId(), type);
        //  24 
        const allWorkIsNovels = idList.every((data) => {
            return data.type === 'novels';
        });
        allWorkIsNovels && (this.onceNumber = 24);
        // 
        const offset = this.getOffset();
        const requsetNumber = this.getRequsetNumber();
        //  id 
        idList.sort(Tools.sortByProperty('id')).reverse();
        //  tag  API 
        //  offset  id
        idList.splice(idList.length - offset, idList.length);
        //  requsetNumber  id
        if (idList.length > requsetNumber) {
            idList.splice(0, idList.length - requsetNumber);
        }
        // 
        store.idList = store.idList.concat(idList);
        this.getIdListFinished();
    }
    //  id  tag
    async getIdListByTag() {
        //  0  tag
        let flag = 'illustmanga';
        switch (this.listType) {
            case 1:
                flag = 'illustmanga';
                break;
            case 2:
                flag = 'illusts';
                break;
            case 3:
                flag = 'manga';
                break;
            case 4:
                flag = 'novels';
                break;
        }
        // 
        const offset = this.getOffset();
        const requsetNumber = this.getRequsetNumber();
        let data = await API.getUserWorksByTypeWithTag(DOM.getUserId(), flag, store.tag, offset, requsetNumber);
        //  illustType 
        if (this.listType === 4) {
            const d = data;
            d.body.works.forEach((data) => store.idList.push({
                type: 'novels',
                id: data.id,
            }));
        }
        else {
            const d = data;
            d.body.works.forEach((data) => {
                let type = 'illusts';
                switch (data.illustType) {
                    case 0:
                        type = 'illusts';
                        break;
                    case 1:
                        type = 'manga';
                        break;
                    case 2:
                        type = 'ugoira';
                        break;
                }
                store.idList.push({
                    type,
                    id: data.id,
                });
            });
        }
        this.getIdListFinished();
    }
    resetGetIdListStatus() {
        this.listType = 0;
    }
    sortResult() {
        //  id id 
        store.result.sort(Tools.sortByProperty('id'));
    }
    destroy() {
        DOM.clearSlot('crawlBtns');
        DOM.clearSlot('otherBtns');
        window.removeEventListener(EVT.list.getIdListFinished, this.sendBookmarkIdList);
    }
}
export { InitUserPage };
