import { EVT } from '../EVT';
import { DOM } from '../DOM';
import { lang } from '../Lang';
import { log } from '../Log';
import { theme } from '../Theme';
import { settings, setSetting } from './Settings';
// 
class SaveNamingRule {
    constructor(ruleInput) {
        this.limit = 20; // 
        this._show = false; // 
        this.html = `
  <div class="saveNamingRuleWrap">
  <button class="nameSave textButton has_tip" type="button" data-tip="${lang.transl('_', this.limit.toString())}">${lang.transl('_')}</button>
  <button class="nameLoad textButton" type="button">${lang.transl('_')}</button>
  <ul class="namingRuleList"></ul>
  </div>`;
        this.ruleInput = ruleInput;
        DOM.clearSlot('saveNamingRule');
        const wrap = DOM.useSlot('saveNamingRule', this.html);
        theme.register(wrap);
        this.saveBtn = wrap.querySelector('button.nameSave');
        this.loadBtn = wrap.querySelector('button.nameLoad');
        this.listWrap = wrap.querySelector('ul.namingRuleList');
        this.createList();
        this.bindEvents();
    }
    set show(boolean) {
        this._show = boolean;
        boolean ? this.showListWrap() : this.hideListWrap();
    }
    get show() {
        return this._show;
    }
    bindEvents() {
        this.saveBtn.addEventListener('click', () => {
            this.add(this.ruleInput.value);
        });
        this.loadBtn.addEventListener('click', () => {
            this.show = !this.show;
        });
        this.listWrap.addEventListener('mouseleave', () => {
            this.show = false;
        });
        // 
        //  data.name data.name 
        window.addEventListener(EVT.list.settingChange, () => {
            this.createList();
        });
    }
    add(rule) {
        if (settings.namingRuleList.length === this.limit) {
            this.delete(0);
        }
        // 
        if (!settings.namingRuleList.includes(rule)) {
            const list = Array.from(settings.namingRuleList);
            list.push(rule);
            setSetting('namingRuleList', list);
        }
        log.success(lang.transl('_'));
    }
    delete(index) {
        const list = Array.from(settings.namingRuleList);
        list.splice(index, 1);
        setSetting('namingRuleList', list);
    }
    select(rule) {
        this.ruleInput.value = rule;
        setSetting('userSetName', rule);
    }
    createList() {
        const htmlArr = [];
        for (let i = 0; i < settings.namingRuleList.length; i++) {
            const html = `<li>
      <span class="rule">${settings.namingRuleList[i]}</span>
      <button class="delete textButton" type="button" data-index="${i}"></button>
    </li>`;
            htmlArr.push(html);
        }
        if (settings.namingRuleList.length === 0) {
            htmlArr.push(`<li><i>&nbsp;&nbsp;&nbsp;&nbsp;no data</i></li>`);
        }
        this.listWrap.innerHTML = htmlArr.join('');
        const ruleEls = this.listWrap.querySelectorAll('.rule');
        for (const el of ruleEls) {
            el.addEventListener('click', () => {
                this.select(el.textContent);
                this.show = false;
            });
        }
        const deleteEls = this.listWrap.querySelectorAll('.delete');
        for (const el of deleteEls) {
            el.addEventListener('click', () => {
                const index = parseInt(el.dataset.index);
                this.delete(index);
            });
        }
    }
    showListWrap() {
        this.listWrap.style.display = 'block';
    }
    hideListWrap() {
        this.listWrap.style.display = 'none';
    }
}
export { SaveNamingRule };
