import { EVT } from '../EVT';
import { lang } from '../Lang';
import { store } from '../Store';
import { DOM } from '../DOM';
import config from '../Config';
import { theme } from '../Theme';
// 
class OutputPanel {
    constructor() {
        this.addOutPutPanel();
        theme.register(this.outputPanel);
        this.bindEvents();
    }
    bindEvents() {
        this.closeBtn.addEventListener('click', () => {
            this.close();
        });
        this.outputPanel.addEventListener('click', (e) => {
            const ev = e || window.event;
            ev.stopPropagation();
        });
        document.addEventListener('click', () => {
            if (this.outputPanel.style.display !== 'none') {
                this.close();
            }
        });
        window.addEventListener(EVT.list.closeCenterPanel, () => {
            this.close();
        });
        // 
        this.copyBtn.addEventListener('click', () => {
            const range = document.createRange();
            range.selectNodeContents(this.outputContent);
            window.getSelection().removeAllRanges();
            window.getSelection().addRange(range);
            document.execCommand('copy');
            // 
            this.copyBtn.textContent = lang.transl('_');
            setTimeout(() => {
                window.getSelection().removeAllRanges();
                this.copyBtn.textContent = lang.transl('_');
            }, 1000);
        });
        window.addEventListener(EVT.list.output, (ev) => {
            this.output(ev.detail.data.content, ev.detail.data.title);
        });
    }
    addOutPutPanel() {
        const html = `
    <div class="outputWrap">
    <div class="outputClose" title="${lang.transl('_')}"></div>
    <div class="outputTitle">${lang.transl('_')}</div>
    <div class="outputContent beautify_scrollbar"></div>
    <div class="outputFooter">
    <button class="outputCopy" title="">${lang.transl('_')}</button>
    </div>
    </div>
    `;
        document.body.insertAdjacentHTML('beforeend', html);
        this.outputPanel = document.querySelector('.outputWrap');
        this.outputTitle = this.outputPanel.querySelector('.outputTitle');
        this.outputContent = this.outputPanel.querySelector('.outputContent');
        this.copyBtn = this.outputPanel.querySelector('.outputCopy');
        this.closeBtn = this.outputPanel.querySelector('.outputClose');
    }
    // 
    output(content, title = lang.transl('_')) {
        //  txt 
        if (store.result.length > config.outputMax) {
            const con = content.replace(/<br>/g, '\n'); // 
            const file = new Blob([con], {
                type: 'text/plain',
            });
            const url = URL.createObjectURL(file);
            const fileName = new Date().toLocaleString() + '.txt';
            DOM.downloadFile(url, fileName);
            // 
            this.copyBtn.disabled = true;
            content = lang.transl('_');
        }
        else {
            this.copyBtn.disabled = false;
        }
        if (content) {
            this.outputContent.innerHTML = content;
            this.outputPanel.style.display = 'block';
            this.outputTitle.textContent = title;
        }
        else {
            return EVT.sendMsg({
                msg: lang.transl('_'),
                type: 'error',
            });
        }
    }
    // 
    close() {
        this.outputPanel.style.display = 'none';
        this.outputContent.innerHTML = '';
    }
}
new OutputPanel();
