import { API } from './API';
import { EVT } from './EVT';
import { DOM } from './DOM';
// 
class Store {
    constructor() {
        this.idList = []; //  id
        this.resultMeta = []; // 
        // 
        this.artworkIDList = []; //  id 
        this.novelIDList = []; //  id 
        this.result = []; // 
        this.rankList = {}; // 
        this.tag = ''; //  tag
        this.title = ''; //  title
        this.crawlCompleteTime = new Date();
        this.bindEvents();
    }
    assignResult(data) {
        // 
        const dataDefault = {
            idNum: 0,
            id: '',
            original: '',
            thumb: '',
            regular: '',
            small: '',
            title: '',
            pageCount: 1,
            dlCount: 1,
            tags: [],
            tagsWithTransl: [],
            tagsTranslOnly: [],
            user: '',
            userId: '',
            fullWidth: 0,
            fullHeight: 0,
            ext: '',
            bmk: 0,
            bookmarked: false,
            date: '',
            type: 0,
            rank: '',
            ugoiraInfo: null,
            seriesTitle: null,
            seriesOrder: null,
            novelMeta: null,
            likeCount: 0,
            viewCount: 0,
            commentCount: 0,
        };
        return Object.assign(dataDefault, data);
    }
    // 
    addResult(data) {
        // 
        if (data.type === 3) {
            if (data.idNum !== undefined) {
                if (this.novelIDList.includes(data.idNum)) {
                    return;
                }
                this.novelIDList.push(data.idNum);
            }
        }
        else {
            if (data.idNum !== undefined) {
                if (this.artworkIDList.includes(data.idNum)) {
                    return;
                }
                this.artworkIDList.push(data.idNum);
            }
        }
        // 
        const result = this.assignResult(data);
        this.resultMeta.push(result);
        EVT.fire(EVT.list.addResult, result);
        if (result.type === 3) {
            this.result.push(result);
        }
        else {
            // 
            for (let i = 0; i < result.dlCount; i++) {
                const result = this.assignResult(data);
                result.idNum = parseInt(result.id);
                result.id = result.id + `_p${i}`;
                result.original = result.original.replace('p0', 'p' + i);
                result.regular = result.regular.replace('p0', 'p' + i);
                result.small = result.small.replace('p0', 'p' + i);
                this.result.push(result);
            }
        }
    }
    getRankList(index) {
        return this.rankList[index];
    }
    setRankList(id, rank) {
        this.rankList[id] = rank;
    }
    reset() {
        this.resultMeta = [];
        this.artworkIDList = [];
        this.novelIDList = [];
        this.result = [];
        this.idList = [];
        this.rankList = {};
        this.tag = API.getTagFromURL();
        this.title = DOM.getTitle();
    }
    bindEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.reset();
        });
        window.addEventListener(EVT.list.resume, () => {
            this.tag = API.getTagFromURL();
            this.title = DOM.getTitle();
        });
    }
}
const store = new Store();
export { store };
