import { EVT } from './EVT';
import { DOM } from './DOM';
import { lang } from './Lang';
import config from './Config';
import { store } from './Store';
import { fileName } from './FileName';
import { createCSV } from './CreateCSV';
// name  csv 
// index 
//  csv 
class ExportResult2CSV {
    constructor() {
        // 
        this.fieldCfg = [
            {
                name: 'id',
                index: 'idNum',
            },
            {
                name: 'tags',
                index: 'tags',
            },
            {
                name: 'tags_transl',
                index: 'tagsTranslOnly',
            },
            {
                name: 'user',
                index: 'user',
            },
            {
                name: 'userId',
                index: 'userId',
            },
            {
                name: 'title',
                index: 'title',
            },
            {
                name: 'type',
                index: 'type',
            },
            {
                name: 'page',
                index: 'pageCount',
            },
            {
                name: 'bookmark',
                index: 'bmk',
            },
            {
                name: 'bookmarked',
                index: 'bookmarked',
            },
            {
                name: 'likeCount',
                index: 'likeCount',
            },
            {
                name: 'viewCount',
                index: 'viewCount',
            },
            {
                name: 'commentCount',
                index: 'commentCount',
            },
            {
                name: 'width',
                index: 'fullWidth',
            },
            {
                name: 'height',
                index: 'fullHeight',
            },
            {
                name: 'date',
                index: 'date',
            },
            {
                name: 'original',
                index: 'original',
            },
            // fileName  index 
            {
                name: 'fileName',
                index: 'title',
            },
        ];
        window.addEventListener(EVT.list.outputCSV, () => {
            this.beforeCreate();
        });
    }
    beforeCreate() {
        // 
        if (store.result.length === 0) {
            EVT.sendMsg({
                msg: lang.transl('_'),
                type: 'error',
            });
            return;
        }
        //  result  resultMeta result resultMeta result 
        // 1 result resultMeta 
        // 2:  A resultMeta  A  B B  resultMeta  A 
        //  result 
        this.create(store.result);
    }
    create(data) {
        const body = []; // 
        // 
        const head = [];
        for (const field of this.fieldCfg) {
            head.push(field.name);
        }
        body.push(head);
        // 
        for (const d of data) {
            // 
            //  csv 
            //  && id  p0 
            if (d.pageCount > 1 && !d.id.endsWith('p0')) {
                continue;
            }
            const bodyItem = []; // 
            // 
            for (const field of this.fieldCfg) {
                if (field.name === 'fileName') {
                    bodyItem.push(fileName.getFileName(d));
                }
                else {
                    let result = d[field.index];
                    if (result === undefined) {
                        result = '';
                    }
                    // 
                    if (field.name === 'type') {
                        result = config.illustTypes[result];
                    }
                    if (field.name === 'bookmarked') {
                        result = result ? 'yes' : 'no';
                    }
                    bodyItem.push(result);
                }
            }
            // 
            body.push(bodyItem);
        }
        // 
        let name = '';
        const ogTitle = document.querySelector('meta[property="og:title"]');
        if (ogTitle) {
            name = ogTitle.content;
        }
        else {
            name = DOM.getTitle();
        }
        createCSV.create({
            body: body,
            download: true,
            fileName: name,
        });
    }
}
new ExportResult2CSV();
