import { Tools } from '../Tools';
class MakeEPUB {
    constructor() { }
    // epub  title  epub 
    make(data, saveMeta = true) {
        return new Promise((resolve, reject) => {
            let content = data.content;
            // 
            if (saveMeta) {
                content = data.meta + content;
            }
            //  br 
            content = content.replace(/\n/g, '<br/>');
            new EpubMaker()
                .withTemplate('idpf-wasteland')
                .withAuthor(Tools.replaceUnsafeStr(data.userName))
                .withModificationDate(new Date(data.createDate))
                .withRights({
                description: data.description,
                license: '',
            })
                .withAttributionUrl(`https://www.pixiv.net/novel/show.php?id=${data.id}`)
                .withCover(data.coverUrl, {
                license: '',
                attributionUrl: '',
            })
                .withTitle(Tools.replaceUnsafeStr(data.title))
                .withSection(new EpubMaker.Section('1', null, { content: content }, false, true))
                .makeEpub()
                .then((blob) => {
                resolve(blob);
            });
        });
    }
}
const makeEPUB = new MakeEPUB();
export { makeEPUB };
