import { lang } from './Lang';
import { EVT } from './EVT';
import { theme } from './Theme';
import { Colors } from './Colors';
// 
class MsgBox {
    constructor() {
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.showMsg, (ev) => {
            const msg = ev.detail.data;
            this.create(msg);
        });
    }
    create(data) {
        const el = document.createElement('div');
        el.classList.add('xz_msg_box');
        let colorStyle = '';
        if (data.type) {
            colorStyle = `style="color:${Colors[data.type]}"`;
        }
        el.innerHTML = `
        <p class="title">${data.title || ''}</p>
        <p class="content" ${colorStyle}>${data.msg}</p>
        <button class="btn" type="button">${data.btn || lang.transl('_')}</button>
      `;
        theme.register(el);
        document.body.insertAdjacentElement('afterbegin', el);
        const btn = el.querySelector('.btn');
        btn.focus();
        if (btn) {
            el.addEventListener('click', (ev) => {
                ev.stopPropagation();
            });
            btn.addEventListener('click', () => {
                this.remove(el);
            });
            document.addEventListener('click', () => {
                this.remove(el);
            });
            window.addEventListener(EVT.list.closeCenterPanel, () => {
                this.remove(el);
            });
        }
    }
    remove(el) {
        el && el.parentNode && el.parentNode.removeChild(el);
    }
}
new MsgBox();
