// 
class EVT {
    // 
    static fire(type, data = {}) {
        const event = new CustomEvent(type, {
            detail: { data: data },
        });
        window.dispatchEvent(event);
    }
    static sendMsg(data) {
        this.fire(this.list.showMsg, data);
    }
}
