import { DOM } from './DOM';
import { EVT } from './EVT';
import { theme } from './Theme';
import { Colors } from './Colors';
// 
class Log {
    constructor() {
        this.logArea = document.createElement('div'); // 
        this.id = 'logWrap'; //  id
        this.refresh = document.createElement('span'); // 
        this.level = ['success', 'warning', 'error'];
        this.toBottom = false; //  true false
        this.scrollToBottom();
        window.addEventListener(EVT.list.clearLog, () => {
            this.clear();
        });
    }
    // 
    checkElement() {
        let test = document.getElementById(this.id);
        if (test === null) {
            this.logArea.id = this.id;
            this.logArea.classList.add('beautify_scrollbar');
            DOM.insertToHead(this.logArea);
            theme.register(this.logArea);
        }
    }
    // 
    clear() {
        this.logArea.innerHTML = '';
    }
    // 
    /*
    str 
    level 
    br 
    keepShow  truefalse 
  
    level 
    -1 auto 
    0 success 
    1 warning 
    2 error 
    */
    add(str, level, br, keepShow) {
        this.checkElement();
        let span = document.createElement('span');
        if (!keepShow) {
            span = this.refresh;
        }
        span.innerHTML = str;
        if (level > -1) {
            span.style.color = Colors[this.level[level]];
        }
        while (br > 0) {
            span.appendChild(document.createElement('br'));
            br--;
        }
        this.logArea.appendChild(span);
        this.toBottom = true; // 
    }
    log(str, br = 1, keepShow = true) {
        this.add(str, -1, br, keepShow);
    }
    success(str, br = 1, keepShow = true) {
        this.add(str, 0, br, keepShow);
    }
    warning(str, br = 1, keepShow = true) {
        this.add(str, 1, br, keepShow);
    }
    error(str, br = 1, keepShow = true) {
        this.add(str, 2, br, keepShow);
    }
    // 
    scrollToBottom() {
        window.setInterval(() => {
            if (this.toBottom) {
                this.logArea.scrollTop = this.logArea.scrollHeight;
                this.toBottom = false;
            }
        }, 800);
    }
}
const log = new Log();
export { log };
