import { EVT } from './EVT';
// 
// 
// 
class States {
    constructor() {
        // 
        //  true false
        // 
        this.busy = false;
        //  true 
        // QuickDownloadBtn  true false
        this.quickDownload = false;
        //  true
        // InitSearchArtworkPage 
        this.notAutoDownload = false;
        // 
        // InitRankingArtworkPage 
        this.debut = false;
        // 
        // 
        //  true false
        this.bookmarkMode = false;
        this.addEvented = false; // 
        this.bindEvents();
    }
    bindEvents() {
        const idle = [
            EVT.list.crawlFinish,
            EVT.list.downloadPause,
            EVT.list.downloadStop,
            EVT.list.downloadComplete,
            EVT.list.bookmarkModeEnd,
        ];
        idle.forEach((type) => {
            window.addEventListener(type, () => {
                this.busy = false;
            });
        });
        const busy = [
            EVT.list.crawlStart,
            EVT.list.downloadStart,
            EVT.list.bookmarkModeStart,
        ];
        busy.forEach((type) => {
            window.addEventListener(type, () => {
                this.busy = true;
            });
        });
        window.addEventListener(EVT.list.bookmarkModeStart, () => {
            this.bookmarkMode = true;
        });
        window.addEventListener(EVT.list.bookmarkModeEnd, () => {
            this.bookmarkMode = false;
        });
    }
}
const states = new States();
export { states };
