// 
import { log } from './Log';
import { lang } from './Lang';
import { Colors } from './Colors';
import { DOM } from './DOM';
import { states } from './States';
import { EVT } from './EVT';
class DeleteWorks {
    constructor(worksSelectors) {
        this.worksSelector = ''; // 
        this.multipleSelector = ''; // 
        this.ugoiraSelector = ''; // 
        this.delMode = false; // 
        this.delBtn = document.createElement('button');
        this.iconId = 'deleteWorkEl';
        this.left = 0;
        this.top = 0;
        this.half = 12;
        this.deleteWorkCallback = () => { }; //  callback 
        this.worksSelector = worksSelectors;
        this.icon = this.createDeleteIcon();
        this.bindEvents();
    }
    createDeleteIcon() {
        const el = document.createElement('div');
        el.id = this.iconId;
        document.body.appendChild(el);
        return el;
    }
    updateDeleteIcon() {
        if (!this.icon) {
            return;
        }
        this.icon.style.display = this.delMode ? 'block' : 'none';
        // 
        if (this.delMode) {
            this.icon.style.left = this.left - this.half + 'px';
            this.icon.style.top = this.top - this.half + 'px';
        }
    }
    bindEvents() {
        // 
        window.addEventListener(EVT.list.worksUpdate, () => {
            if (this.delMode) {
                this.bindDeleteEvent();
            }
        });
        // 
        window.addEventListener(EVT.list.pageSwitch, () => {
            if (this.delMode) {
                this.toggleDeleteMode();
            }
        });
        // 
        window.addEventListener('mousemove', (ev) => {
            this.moveEvent(ev);
        }, true);
    }
    // 
    moveEvent(ev) {
        this.left = ev.x;
        this.top = ev.y;
        this.updateDeleteIcon();
    }
    // 
    addClearMultipleBtn(selector, callback = () => { }) {
        this.multipleSelector = selector;
        DOM.addBtn('crawlBtns', Colors.red, lang.transl('_'), [
            ['title', lang.transl('_Title')],
        ]).addEventListener('click', () => {
            if (states.busy) {
                EVT.sendMsg({
                    msg: lang.transl('_'),
                    type: 'error',
                });
                return;
            }
            EVT.fire(EVT.list.closeCenterPanel);
            this.clearMultiple();
            callback();
        }, false);
    }
    // 
    addClearUgoiraBtn(selector, callback = () => { }) {
        this.ugoiraSelector = selector;
        DOM.addBtn('crawlBtns', Colors.red, lang.transl('_'), [
            ['title', lang.transl('_Title')],
        ]).addEventListener('click', () => {
            if (states.busy) {
                EVT.sendMsg({
                    msg: lang.transl('_'),
                    type: 'error',
                });
                return;
            }
            EVT.fire(EVT.list.closeCenterPanel);
            this.ClearUgoira();
            callback();
        }, false);
    }
    // 
    addManuallyDeleteBtn(callback = () => { }) {
        this.deleteWorkCallback = callback;
        this.delBtn = DOM.addBtn('crawlBtns', Colors.red, lang.transl('_'), [['title', lang.transl('_Title')]]);
        this.delBtn.addEventListener('click', () => {
            this.toggleDeleteMode();
        });
    }
    // 
    toggleDeleteMode() {
        this.delMode = !this.delMode;
        this.bindDeleteEvent();
        this.updateDeleteIcon();
        if (this.delMode) {
            this.delBtn.textContent = lang.transl('_');
            setTimeout(() => {
                EVT.fire(EVT.list.closeCenterPanel);
            }, 100);
        }
        else {
            this.delBtn.textContent = lang.transl('_');
        }
    }
    // 
    clearMultiple() {
        const allPicArea = document.querySelectorAll(this.worksSelector);
        allPicArea.forEach((el) => {
            if (el.querySelector(this.multipleSelector)) {
                el.remove();
            }
        });
        this.showWorksCount();
    }
    // 
    ClearUgoira() {
        const allPicArea = document.querySelectorAll(this.worksSelector);
        allPicArea.forEach((el) => {
            if (el.querySelector(this.ugoiraSelector)) {
                el.remove();
            }
        });
        this.showWorksCount();
    }
    // 
    // 
    bindDeleteEvent() {
        const listElement = document.querySelectorAll(this.worksSelector);
        listElement.forEach((el) => {
            el.onclick = (ev) => {
                if (this.delMode) {
                    ev.preventDefault();
                    if (states.busy) {
                        EVT.sendMsg({
                            msg: lang.transl('_'),
                            type: 'error',
                        });
                        return;
                    }
                    const target = ev.currentTarget;
                    DOM.removeEl(target);
                    this.showWorksCount();
                    this.deleteWorkCallback(target);
                }
            };
        });
    }
    // 
    showWorksCount() {
        const selector = this.worksSelector;
        log.success(lang.transl('_', DOM.getVisibleEl(selector).length.toString()), 2, false);
    }
}
export { DeleteWorks };
