// settings 
//  settings  EVT.list.settingChange 
//  settingssettingChange 
// settingChange  {name:string, value:any}
//  settings  localStorage  settings  localStorage 
import { EVT } from '../EVT';
import { DOM } from '../DOM';
import { Tools } from '../Tools';
import { convertOldSettings } from './ConvertOldSettings';
class Settings {
    constructor() {
        this.storeName = 'xzSetting';
        // 
        this.defaultSettings = {
            setWantPage: -1,
            wantPageArr: [
                -1,
                -1,
                -1,
                -1,
                -1,
                1000,
                -1,
                500,
                -1,
                1000,
                100,
                -1,
                100,
                -1,
                -1,
                1000,
                100,
                100,
                100,
                100,
                -1,
            ],
            firstFewImagesSwitch: false,
            firstFewImages: 1,
            downType0: true,
            downType1: true,
            downType2: true,
            downType3: true,
            downSingleImg: true,
            downMultiImg: true,
            downColorImg: true,
            downBlackWhiteImg: true,
            downNotBookmarked: true,
            downBookmarked: true,
            ugoiraSaveAs: 'webm',
            convertUgoiraThread: 1,
            needTag: [],
            notNeedTag: [],
            quietDownload: true,
            downloadThread: 5,
            userSetName: '{id}',
            namingRuleList: [],
            tagNameToFileName: false,
            alwaysFolder: false,
            workDir: false,
            workDirFileNumber: 1,
            workDirName: 'id',
            showOptions: true,
            postDate: false,
            postDateStart: 946684800000,
            postDateEnd: 4102444800000,
            previewResult: true,
            BMKNumSwitch: false,
            BMKNumMin: 0,
            BMKNumMax: 999999,
            BMKNumAverageSwitch: false,
            BMKNumAverage: 600,
            setWHSwitch: false,
            widthHeightLimit: '>=',
            setWidthAndOr: '&',
            setWidth: 0,
            setHeight: 0,
            ratioSwitch: false,
            ratio: 'horizontal',
            userRatio: 1.4,
            idRangeSwitch: false,
            idRangeInput: 0,
            idRange: '>',
            needTagSwitch: false,
            notNeedTagSwitch: false,
            noSerialNo: false,
            filterBlackWhite: false,
            sizeSwitch: false,
            sizeMin: 0,
            sizeMax: 100,
            novelSaveAs: 'txt',
            saveNovelMeta: false,
            deduplication: false,
            dupliStrategy: 'loose',
            fileNameLengthLimitSwitch: false,
            fileNameLengthLimit: 200,
            imageSize: 'original',
            dateFormat: 'YYYY-MM-DD',
            userSetLang: 'auto',
            bmkAfterDL: false,
            widthTag: 'yes',
            restrict: 'no',
            userBlockList: false,
            blockList: [],
            theme: 'auto',
            needTagMode: 'all',
        };
        this.allSettingKeys = Object.keys(this.defaultSettings);
        this.floatNumberKey = ['userRatio', 'sizeMin', 'sizeMax'];
        this.numberArrayKey = ['wantPageArr'];
        this.stringArrayKey = [
            'namingRuleList',
            'blockList',
            'needTag',
            'notNeedTag',
        ];
        // 
        this.settings = Tools.deepCopy(this.defaultSettings);
        // 
        this.restoreSettings();
        this.bindEvents();
    }
    bindEvents() {
        // 
        window.addEventListener(EVT.list.settingChange, () => {
            localStorage.setItem(this.storeName, JSON.stringify(this.settings));
        });
        window.addEventListener(EVT.list.resetSettings, () => {
            this.reset();
        });
        window.addEventListener(EVT.list.exportSettings, () => {
            this.exportSettings();
        });
        window.addEventListener(EVT.list.importSettings, () => {
            this.importSettings();
        });
    }
    // 
    restoreSettings() {
        const savedSettings = localStorage.getItem(this.storeName);
        if (savedSettings) {
            this.assignSettings(JSON.parse(savedSettings));
        }
    }
    //  settings 
    // 
    // 1.  number  stringsetSetting 
    // 2. 
    assignSettings(data) {
        const origin = Tools.deepCopy(data);
        for (const [key, value] of Object.entries(origin)) {
            this.setSetting(key, value, false);
        }
        // 
        EVT.fire(EVT.list.settingChange);
    }
    exportSettings() {
        const str = JSON.stringify(settings, null, 2);
        const blob = new Blob([str], { type: 'application/json' });
        const url = URL.createObjectURL(blob);
        DOM.downloadFile(url, `pixiv_batch_downloader-settings.json`);
    }
    async importSettings() {
        const loadedJSON = (await DOM.loadJSONFile().catch((err) => {
            return EVT.sendMsg({
                type: 'error',
                msg: err,
            });
        }));
        if (!loadedJSON) {
            return;
        }
        // 
        if (loadedJSON.downloadThread === undefined) {
            return EVT.sendMsg({
                type: 'error',
                msg: 'Format error!',
            });
        }
        // 
        this.reset(loadedJSON);
    }
    // 
    // 
    reset(data) {
        if (data) {
            this.assignSettings(data);
        }
        else {
            // 
            this.assignSettings(this.defaultSettings);
        }
        EVT.fire(EVT.list.resetSettingsEnd);
    }
    tipError(key) {
        EVT.sendMsg({
            msg: `${key}: Invalid value`,
            type: 'error',
        });
    }
    // 
    // 
    // 
    // 1.  string  'a,b,c' string[]  ['a','b','c']
    // 2.  string[] string  'a,b,c' string[]
    setSetting(key, value, fireEvt = true) {
        if (!this.allSettingKeys.includes(key)) {
            return;
        }
        const keyType = typeof this.defaultSettings[key];
        const valueType = typeof value;
        //  string 
        if (valueType === 'string') {
            value = convertOldSettings.convert(key, value);
        }
        // 
        if (keyType === 'string' && valueType !== 'string') {
            value = value.toString();
        }
        if (keyType === 'number' && valueType !== 'number') {
            //  number 
            if (key === 'postDateStart' || key == 'postDateEnd') {
                if (valueType === 'string') {
                    if (value === '') {
                        return this.tipError(key);
                    }
                    const date = new Date(value);
                    value = date.getTime();
                }
            }
            else {
                //  number 
                if (this.floatNumberKey.includes(key)) {
                    value = Number.parseFloat(value);
                }
                else {
                    value = Number.parseInt(value);
                }
            }
            if (isNaN(value)) {
                return this.tipError(key);
            }
        }
        if (keyType === 'boolean' && valueType !== 'boolean') {
            value = !!value;
        }
        if (Array.isArray(this.defaultSettings[key])) {
            if (this.stringArrayKey.includes(key)) {
                //  string[]
                if (valueType === 'string') {
                    value = Tools.string2array(value);
                }
            }
            if (this.numberArrayKey.includes(key)) {
                //  number[]
                if (Array.isArray(value)) {
                    value = value.map((val) => {
                        if (typeof val !== 'number') {
                            return Number(val);
                        }
                        else {
                            return val;
                        }
                    });
                }
                else {
                    return;
                }
            }
        }
        // 
        ;
        this.settings[key] = value;
        // 
        //  fireEvt  false
        if (fireEvt) {
            EVT.fire(EVT.list.settingChange, { name: key, value: value });
        }
    }
}
const self = new Settings();
const settings = self.settings;
const setSetting = self.setSetting.bind(self);
export { settings, setSetting };
