import { DOM } from './DOM';
import { Colors } from './Colors';
import { lang } from './Lang';
import { EVT } from './EVT';
import { states } from './States';
// 
class SelectWork {
    constructor() {
        this.created = false;
        this.selectorId = 'selectWorkEl';
        this.left = 0;
        this.top = 0;
        this.half = 10; // 
        this._start = false;
        this._pause = false;
        this._tempHide = false; //  display
        this.controlBtn = document.createElement('button'); // 
        this.crawlBtn = document.createElement('button'); // 
        this.clearBtn = document.createElement('button'); // 
        this.selectedWorkFlagClass = 'selectedWorkFlag'; //  class
        this.positionValue = ['relative', 'absolute', 'fixed']; // 
        this.artworkReg = /artworks\/(\d{2,15})/;
        this.novelReg = /novel\/show\.php\?id=(\d{2,15})/;
        // 
        // root ; js-mount-point-discovery 
        this.worksWrapperSelectorList = [
            '#root',
            '#js-mount-point-discovery',
        ];
        // 
        this.usedWorksWrapperSelector = this.worksWrapperSelectorList[0];
        this.idList = [];
        this.observeTimer = 0;
        this.sendCrawl = false; // 
        this.crawled = false; // 
        if (!this.created && location.hostname.endsWith('.pixiv.net')) {
            this.created = true;
            this.selector = this.createSelectorEl();
            this.addBtn();
            this.bindEvents();
        }
    }
    get start() {
        return this._start;
    }
    set start(bool) {
        this._start = bool;
        this.updateSelectorEl();
        this.updateControlBtn();
    }
    get pause() {
        return this._pause;
    }
    set pause(bool) {
        this._pause = bool;
        this.updateSelectorEl();
        this.updateControlBtn();
    }
    get tempHide() {
        return this._tempHide;
    }
    set tempHide(bool) {
        this._tempHide = bool;
        this.updateSelectorEl();
    }
    bindEvents() {
        window.addEventListener(EVT.list.openCenterPanel, () => {
            this.tempHide = true;
        });
        window.addEventListener(EVT.list.closeCenterPanel, () => {
            this.tempHide = false;
        });
        //  id 
        window.addEventListener(EVT.list.crawlFinish, () => {
            if (this.sendCrawl) {
                this.sendCrawl = false;
                this.crawled = true;
            }
        });
        //  Alt + S 
        window.addEventListener('keydown', (ev) => {
            if (ev.altKey && ev.code === 'KeyS') {
                this.controlBtn.click();
            }
        });
        // 
        window.addEventListener('mousemove', (ev) => {
            this.moveEvent(ev);
        }, true);
        // 
        window.onbeforeunload = () => {
            // 
            if (this.idList.length > 0 && !this.crawled) {
                EVT.sendMsg({
                    msg: lang.transl('_'),
                    type: 'error',
                });
                return false;
            }
        };
        // 
        //  pixiv 
        //  dom  dom 
        //  dom  reAddAllFlag 
        window.addEventListener(EVT.list.pageSwitch, () => {
            // 
            let worksWrapper = null;
            for (const selector of this.worksWrapperSelectorList) {
                worksWrapper = document.querySelector(selector);
                if (worksWrapper) {
                    this.usedWorksWrapperSelector = selector;
                    break;
                }
            }
            if (worksWrapper === null) {
                return;
            }
            // 
            const ob = new MutationObserver((records) => {
                window.clearTimeout(this.observeTimer);
                this.observeTimer = window.setTimeout(() => {
                    this.reAddAllFlag();
                }, 300);
                // 
            });
            ob.observe(worksWrapper, {
                childList: true,
                subtree: true,
            });
        });
    }
    clearIdList() {
        //  id  id
        this.removeAllSelectedFlag();
        this.idList = [];
        this.updateCrawlBtn();
    }
    createSelectorEl() {
        const el = document.createElement('div');
        el.id = this.selectorId;
        document.body.appendChild(el);
        return el;
    }
    updateSelectorEl() {
        if (!this.selector) {
            return;
        }
        const show = this.start && !this.pause && !this.tempHide;
        this.selector.style.display = show ? 'block' : 'none';
        //  style  display  noneChrome 
        // 
        if (show) {
            this.selector.style.left = this.left - this.half + 'px';
            this.selector.style.top = this.top - this.half + 'px';
        }
    }
    addBtn() {
        this.controlBtn = DOM.addBtn('selectWorkBtns', Colors.green, lang.transl('_'));
        this.updateControlBtn();
        this.clearBtn = DOM.addBtn('selectWorkBtns', Colors.red, lang.transl('_'));
        this.clearBtn.style.display = 'none';
        this.clearBtn.addEventListener('click', () => {
            this.clearIdList();
        });
        this.crawlBtn = DOM.addBtn('selectWorkBtns', Colors.blue, lang.transl('_'));
        this.crawlBtn.style.display = 'none';
        this.crawlBtn.addEventListener('click', (ev) => {
            this.downloadSelect();
        });
    }
    // 
    updateControlBtn() {
        if (!this.start) {
            this.controlBtn.textContent = lang.transl('_');
            this.controlBtn.onclick = (ev) => {
                this.startSelect(ev);
                this.clearBtn.style.display = 'block';
            };
        }
        else {
            if (!this.pause) {
                this.controlBtn.textContent = lang.transl('_');
                this.controlBtn.onclick = (ev) => {
                    this.pauseSelect();
                };
            }
            else {
                this.controlBtn.textContent = lang.transl('_');
                this.controlBtn.onclick = (ev) => {
                    this.startSelect(ev);
                };
            }
        }
    }
    // 
    updateCrawlBtn() {
        this.crawlBtn.style.display = this.start ? 'block' : 'none';
        if (this.idList.length > 0) {
            this.crawlBtn.textContent =
                lang.transl('_') + ` ${this.idList.length}`;
        }
        else {
            this.crawlBtn.textContent = lang.transl('_');
        }
    }
    // 
    clickEvent(ev) {
        const workId = this.findWork(ev.path || ev.composedPath());
        if (workId) {
            // 
            ev.preventDefault();
            // 
            const index = this.idList.findIndex((item) => {
                return item.id === workId.id && item.type === workId.type;
            });
            //  id  idList 
            if (index === -1) {
                this.idList.push(workId);
                this.crawled = false;
                this.addSelectedFlag(ev.target, workId.id);
            }
            else {
                // id 
                this.idList.splice(index, 1);
                this.removeSelectedFlag(workId.id);
            }
            this.updateCrawlBtn();
        }
    }
    // 
    // 
    moveEvent(ev) {
        this.left = ev.x;
        this.top = ev.y;
        this.updateSelectorEl();
    }
    // esc 
    escEvent(ev) {
        if (ev.code === 'Escape') {
            this.pauseSelect();
        }
    }
    // 
    startSelect(ev) {
        this.start = true;
        if (this.pause) {
            // 
            this.pause = false;
        }
        else {
            // 
            this.clearIdList();
        }
        this.bindClickEvent = this.clickEvent.bind(this);
        this.bindEscEvent = this.escEvent.bind(this);
        window.addEventListener('click', this.bindClickEvent, true);
        document.addEventListener('keyup', this.bindEscEvent);
        EVT.fire(EVT.list.closeCenterPanel);
    }
    pauseSelect() {
        this.pause = true;
        this.bindClickEvent &&
            window.removeEventListener('click', this.bindClickEvent, true);
        this.bindEscEvent &&
            document.removeEventListener('keyup', this.bindEscEvent);
    }
    // 
    downloadSelect() {
        if (states.busy) {
            EVT.sendMsg({
                msg: lang.transl('_'),
                type: 'error',
            });
            return;
        }
        this.pauseSelect();
        if (this.idList.length > 0) {
            //  id  id 
            EVT.fire(EVT.list.downloadIdList, Array.from(this.idList));
            this.sendCrawl = true;
            this.crawled = false;
        }
        else {
            EVT.sendMsg({
                msg: lang.transl('_'),
                type: 'error',
            });
        }
    }
    //  id
    findWork(arr) {
        for (const el of arr) {
            //  a 
            if (el.nodeName === 'A') {
                const href = el.href;
                // 
                const test = this.artworkReg.exec(href);
                if (test && test[1]) {
                    return {
                        type: 'unknown',
                        id: test[1],
                    };
                }
                // 
                const test2 = this.novelReg.exec(href);
                if (test2 && test2[1]) {
                    return {
                        type: 'novels',
                        id: test2[1],
                    };
                }
            }
        }
    }
    // 
    addSelectedFlag(el, id) {
        var _a;
        const span = document.createElement('span');
        span.classList.add(this.selectedWorkFlagClass);
        span.dataset.id = id;
        let target = el;
        //  svg  svg  svg 
        //  svg  pixiv 
        if (el.nodeName === 'svg' || ((_a = el.parentElement) === null || _a === void 0 ? void 0 : _a.nodeName) === 'svg') {
            target = el.parentElement;
        }
        target.insertAdjacentElement('beforebegin', span);
        // 
        if (target.parentElement) {
            const position = window.getComputedStyle(target.parentElement)['position'];
            if (!this.positionValue.includes(position)) {
                target.parentElement.style.position = 'relative';
            }
        }
    }
    // 
    reAddAllFlag() {
        if (this.idList.length === 0) {
            return;
        }
        for (const { id, type } of this.idList) {
            if (this.getSelectedFlag(id)) {
                // 
                /**
                 *  https://www.pixiv.net/users/18095070/illustrations
                 *  tag  https://www.pixiv.net/users/18095070/illustrations/%E5%A5%B3%E3%81%AE%E5%AD%90
                 * pixiv 
                 */
                return;
            }
            let el;
            if (type === 'novels') {
                el = document.querySelector(`${this.usedWorksWrapperSelector} a[href="/novel/show.php?id=${id}"]`);
            }
            else {
                el = document.querySelector(`${this.usedWorksWrapperSelector} a[href="/artworks/${id}"]`);
            }
            if (el) {
                // 
                this.addSelectedFlag(el, id);
            }
        }
    }
    getSelectedFlag(id) {
        return document.querySelector(`.${this.selectedWorkFlagClass}[data-id='${id}']`);
    }
    // 
    removeSelectedFlag(id) {
        const el = this.getSelectedFlag(id);
        el && el.remove();
    }
    // 
    removeAllSelectedFlag() {
        for (const item of this.idList) {
            this.removeSelectedFlag(item.id);
        }
    }
}
new SelectWork();
