//  bookmark_detail 
import { InitPageBase } from '../InitPageBase';
import { Colors } from '../Colors';
import { lang } from '../Lang';
import { DOM } from '../DOM';
import { options } from '../setting/Options';
import { API } from '../API';
import { store } from '../Store';
class InitBookmarkDetailPage extends InitPageBase {
    constructor() {
        super();
        this.init();
    }
    addCrawlBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        }, false);
    }
    initAny() { }
    setFormOption() {
        // /
        options.setWantPageTip({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: `1 - ${this.maxCount}`,
        });
    }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInputGreater0(this.maxCount, false);
    }
    // 
    async getIdList() {
        let data = await API.getRecommenderData(API.getIllustId(), this.crawlNumber);
        for (const id of data.recommendations) {
            store.idList.push({
                type: 'unknown',
                id: id.toString(),
            });
        }
        this.getIdListFinished();
    }
    resetGetIdListStatus() { }
}
export { InitBookmarkDetailPage };
