import { lang } from './Lang';
import { API } from './API';
import { log } from './Log';
import { DOM } from './DOM';
import { EVT } from './EVT';
// 
class SaveAvatarImage {
    constructor() {
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.saveAvatarImage, () => {
            this.saveAvatarImage();
        });
    }
    async saveAvatarImage() {
        const userId = DOM.getUserId();
        const userProfile = await API.getUserProfile(userId);
        const bigImg = userProfile.body.imageBig; // imageBig  170 px 
        const fullSizeImg = bigImg.replace('_170', ''); //  170 
        // 
        const img = await fetch(fullSizeImg);
        const blob = await img.blob();
        // 
        const arr = fullSizeImg.split('.');
        const ext = arr[arr.length - 1];
        // 
        const url = URL.createObjectURL(blob);
        const name = `${userProfile.body.name}_${userId}_avatar.${ext}`;
        DOM.downloadFile(url, name);
        log.success(' ' + lang.transl('_'));
        EVT.fire(EVT.list.closeCenterPanel);
    }
}
new SaveAvatarImage();
