import { API } from './API';
import { token } from './Token';
import { store } from './Store';
import { settings } from './setting/Settings';
import { lang } from './Lang';
import { EVT } from './EVT';
// 
class BookmarkAfterDL {
    constructor(tipEl) {
        //  id
        // 
        this.savedIds = [];
        this.successCount = 0;
        this.tipEl = document.createElement('span');
        if (tipEl) {
            this.tipEl = tipEl;
        }
        this.bindEvents();
    }
    // 
    bindEvents() {
        //  id 
        window.addEventListener(EVT.list.downloadSuccess, (ev) => {
            const successData = ev.detail.data;
            this.send(Number.parseInt(successData.id));
        });
        // 
        // 
        // 
        window.addEventListener(EVT.list.skipDownload, (ev) => {
            const skipData = ev.detail.data;
            if (skipData.reason === 'duplicate') {
                this.send(Number.parseInt(skipData.id));
            }
        });
        // 
        window.addEventListener(EVT.list.crawlStart, (ev) => {
            this.reset();
            this.showProgress();
        });
    }
    showProgress() {
        if (this.savedIds.length === 0) {
            return (this.tipEl.textContent = '');
        }
        this.tipEl.textContent = `${lang.transl('_')} ${this.successCount}/${this.savedIds.length}`;
    }
    reset() {
        this.savedIds = [];
        this.successCount = 0;
    }
    //  id
    send(id) {
        if (!settings.bmkAfterDL) {
            return;
        }
        if (typeof id !== 'number') {
            id = Number.parseInt(id);
        }
        //  id 
        if (this.savedIds.includes(id)) {
            return;
        }
        this.addBookmark(id);
    }
    //  tag  tag
    async addBookmark(id) {
        return new Promise(async (resolve, reject) => {
            this.savedIds.push(id);
            this.showProgress();
            //  store 
            let data = undefined;
            let dataSource = store.resultMeta.length > 0 ? store.resultMeta : store.result;
            for (const r of dataSource) {
                if (r.idNum === id) {
                    data = r;
                    break;
                }
            }
            if (data === undefined) {
                return reject(new Error(`Not find ${id} in result`));
            }
            await API.addBookmark(data.type !== 3 ? 'illusts' : 'novels', id.toString(), settings.widthTag === 'yes' ? data.tags : [], settings.restrict === 'yes', token.token).catch((err) => {
                //  id 
                console.error(err);
                const len = this.savedIds.length;
                for (let index = 0; index < len; index++) {
                    if (this.savedIds[index] === id) {
                        delete this.savedIds[index];
                        break;
                    }
                }
                return resolve(this.send(id));
            });
            this.successCount++;
            this.showProgress();
            resolve();
        });
    }
}
export { BookmarkAfterDL };
