// 
import { pageType } from './PageType';
import { EVT } from './EVT';
/*
 [flag]  title 
flag 
 
 
 
 
 
 
 
*/
const flags = {
    crawling: '',
    waiting: '',
    readyDownload: '',
    downloading: '',
    paused: '',
    stopped: '',
    completed: '',
    space: ' ',
};
class TitleBar {
    constructor() {
        this.timer = 0; // title 
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.set('crawling');
        });
        window.addEventListener(EVT.list.worksUpdate, () => {
            this.set('waiting');
        });
        for (const ev of [
            EVT.list.crawlFinish,
            EVT.list.resultChange,
            EVT.list.resume,
        ]) {
            window.addEventListener(ev, () => {
                this.set('readyDownload');
            });
        }
        window.addEventListener(EVT.list.downloadStart, () => {
            this.set('downloading');
        });
        window.addEventListener(EVT.list.downloadComplete, () => {
            this.set('completed');
        });
        window.addEventListener(EVT.list.downloadPause, () => {
            this.set('paused');
        });
        window.addEventListener(EVT.list.downloadStop, () => {
            this.set('stopped');
        });
        window.addEventListener(EVT.list.crawlEmpty, () => {
            this.reset();
        });
    }
    // 
    includeFlag(flag = '') {
        if (!flag) {
            // 
            for (const flga of Object.values(flags)) {
                const str = `[${flga}]`;
                if (document.title.includes(str)) {
                    return true;
                }
            }
        }
        else {
            // 
            const str = `[${flag}]`;
            return document.title.includes(str);
        }
        return false;
    }
    //  title
    reset() {
        clearInterval(this.timer);
        if (pageType.type == 1 || pageType.type === 2 || pageType.type === 13) {
            //  og:title og:title  og:title 
            const ogTitle = document.querySelector('meta[property="og:title"]');
            if (ogTitle) {
                document.title = ogTitle.content;
                return;
            }
        }
        //  title 
        const index = document.title.indexOf(']');
        document.title = document.title.substr(index + 1, document.title.length);
    }
    // 
    set(flagName) {
        const flag = flags[flagName];
        const text = `[${flag}]`;
        //  title 
        if (!this.includeFlag()) {
            document.title = `${text} ${document.title}`;
        }
        else {
            // 
            document.title = document.title.replace(/\[.?\]/, text);
        }
        // 
        if (flagName === 'readyDownload' || flagName === 'waiting') {
            this.flashing(flag);
        }
        else {
            clearInterval(this.timer);
        }
    }
    // 
    flashing(flag) {
        clearInterval(this.timer);
        const text = `[${flag}]`;
        const whiteSpace = `[${flags.space}]`;
        this.timer = window.setInterval(() => {
            if (this.includeFlag(flag)) {
                // 
                document.title = document.title.replace(text, whiteSpace);
            }
            else {
                if (this.includeFlag(flags.space)) {
                    // 
                    document.title = document.title.replace(whiteSpace, text);
                }
                else {
                    // 
                    clearInterval(this.timer);
                }
            }
        }, 500);
    }
}
new TitleBar();
