// artwork 
import { InitPageBase } from '../InitPageBase';
import { Colors } from '../Colors';
import { EVT } from '../EVT';
import { lang } from '../Lang';
import { options } from '../setting/Options';
import { store } from '../Store';
import { QuickBookmark } from '../QuickBookmark';
import { ImgViewer } from '../ImgViewer';
import { DOM } from '../DOM';
import { API } from '../API';
import { log } from '../Log';
import { QuickDownloadBtn } from '../QuickDownloadBtn';
import { states } from '../States';
import { Tools } from '../Tools';
class InitArtworkPage extends InitPageBase {
    constructor() {
        super();
        this.crawlDirection = 0; // 
        /*
        -1 
        0 
        1 
        */
        this.crawlRelated = false; // 
        this.startQuickDownload = () => {
            this.readyCrawl();
        };
        this.init();
    }
    initAny() {
        // 
        this.initQuickBookmark();
        this.initImgViewer();
        // 
        window.addEventListener(EVT.list.pageSwitchedTypeNotChange, this.initQuickBookmark);
        window.addEventListener(EVT.list.pageSwitchedTypeNotChange, this.initImgViewer);
        // 
        new QuickDownloadBtn();
        window.addEventListener(EVT.list.QuickDownload, this.startQuickDownload);
    }
    initImgViewer() {
        new ImgViewer();
    }
    initQuickBookmark() {
        new QuickBookmark();
    }
    addCrawlBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_new')).addEventListener('click', () => {
            this.crawlDirection = -1;
            this.readyCrawl();
        });
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_old')).addEventListener('click', () => {
            this.crawlDirection = 1;
            this.readyCrawl();
        });
        const downRelatedBtn = DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'));
        downRelatedBtn.addEventListener('click', () => {
            this.crawlRelated = true;
            this.readyCrawl();
        }, false);
    }
    setFormOption() {
        // /
        options.setWantPageTip({
            text: lang.transl('_'),
            tip: lang.transl('_') +
                '<br>' +
                lang.transl('_0'),
            rangTip: lang.transl('_1'),
        });
    }
    destroy() {
        DOM.clearSlot('crawlBtns');
        DOM.clearSlot('otherBtns');
        // 
        window.removeEventListener(EVT.list.pageSwitchedTypeNotChange, this.initQuickBookmark);
        window.removeEventListener(EVT.list.pageSwitchedTypeNotChange, this.initImgViewer);
        window.removeEventListener(EVT.list.QuickDownload, this.startQuickDownload);
    }
    getWantPage() {
        if (states.quickDownload) {
            // 
            this.crawlNumber = 1;
        }
        else {
            // 
            if (!this.crawlRelated) {
                const crawlAllTip = this.crawlDirection === -1
                    ? lang.transl('_new')
                    : lang.transl('_old');
                this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), crawlAllTip);
            }
            else {
                // 
                this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), lang.transl('_'));
            }
        }
    }
    nextStep() {
        // 
        if (this.crawlRelated) {
            this.getRelatedList();
        }
        else if (states.quickDownload) {
            // 
            store.idList.push({
                type: 'unknown',
                id: API.getIllustId(window.location.href),
            });
            log.log(lang.transl('_'));
            this.getIdListFinished();
        }
        else {
            // 
            this.getIdList();
        }
    }
    async getIdList() {
        let type = ['illusts', 'manga'];
        let idList = await API.getUserWorksByType(DOM.getUserId(), type);
        //  id
        let nowId = parseInt(API.getIllustId(window.location.href));
        idList.forEach((id) => {
            let idNum = parseInt(id.id);
            // 
            if (idNum >= nowId && this.crawlDirection === -1) {
                store.idList.push(id);
            }
            else if (idNum <= nowId && this.crawlDirection === 1) {
                // 
                store.idList.push(id);
            }
        });
        // 
        if (this.crawlNumber !== -1) {
            //  
            if (this.crawlDirection === -1) {
                store.idList.sort(Tools.sortByProperty('id')).reverse();
            }
            else {
                //  
                store.idList.sort(Tools.sortByProperty('id'));
            }
            store.idList = store.idList.splice(0, this.crawlNumber);
        }
        this.getIdListFinished();
    }
    // 
    async getRelatedList() {
        let data = await API.getRelatedData(API.getIllustId());
        // 
        let ids = [];
        for (const illust of data.body.illusts) {
            ids.push(illust.id);
        }
        ids = ids.concat(data.body.nextIds);
        // 
        if (this.crawlNumber !== -1) {
            ids = ids.slice(0, this.crawlNumber);
        }
        for (const id of ids) {
            store.idList.push({
                type: 'unknown',
                id,
            });
        }
        log.log(lang.transl('_', store.idList.length.toString()));
        this.getIdListFinished();
    }
    resetGetIdListStatus() {
        this.crawlDirection = 0; // 
        this.crawlRelated = false; // 
    }
}
export { InitArtworkPage };
