import { langText } from './langText';
import { EVT } from './EVT';
import { settings } from './setting/Settings';
// 
class Lang {
    constructor() {
        this.flag = 'zh-cn';
        this.flagIndex = {
            'zh-cn': 0,
            'ja': 1,
            'en': 2,
            'zh-tw': 3,
        };
        this.setFlag();
        this.bindEvents();
    }
    bindEvents() {
        // 
        window.addEventListener(EVT.list.settingChange, () => {
            const old = this.flag;
            this.setFlag();
            if (this.flag !== old) {
                EVT.sendMsg({
                    msg: this.transl('_'),
                });
            }
        });
    }
    setFlag() {
        this.flag = settings.userSetLang === 'auto' ? this.getLangType() : settings.userSetLang;
    }
    //  flag
    getLangType() {
        const userLang = document.documentElement.lang;
        switch (userLang) {
            case 'zh':
            case 'zh-CN':
            case 'zh-Hans':
                return 'zh-cn'; // 
            case 'ja':
                return 'ja'; // 
            case 'zh-Hant':
            case 'zh-tw':
            case 'zh-TW':
                return 'zh-tw'; // 
            default:
                return 'en'; // English
        }
    }
    // translate 
    transl(name, ...arg) {
        let content = langText[name][this.flagIndex[this.flag]];
        arg.forEach((val) => (content = content.replace('{}', val)));
        return content;
    }
}
const lang = new Lang();
export { lang };
