// 
import { EVT } from './EVT';
import { log } from './Log';
import { lang } from './Lang';
import { DOM } from './DOM';
import { fileName } from './FileName';
import { converter } from './ugoira/ConvertUgoira';
import { progressBar } from './ProgressBar';
import { filter } from './Filter';
import { deduplication } from './Deduplication';
import { settings } from './setting/Settings';
import { MakeNovelFile } from './novel/MakeNovelFile';
class Download {
    constructor(progressBarIndex, data) {
        this.fileName = '';
        this.retryMax = 30;
        this.retry = 0; // 
        this.cancel = false; // 
        this.sizeChecked = false; // 
        this.progressBarIndex = progressBarIndex;
        this.download(data);
        this.listenEvents();
    }
    listenEvents() {
        ;
        [EVT.list.downloadStop, EVT.list.downloadPause].forEach((event) => {
            window.addEventListener(event, () => {
                this.cancel = true;
            });
        });
    }
    // 
    setProgressBar(loaded, total) {
        progressBar.setProgress(this.progressBarIndex, {
            name: this.fileName,
            loaded: loaded,
            total: total,
        });
    }
    skip(data, msg = '') {
        this.cancel = true;
        log.warning(msg);
        EVT.fire(EVT.list.skipDownload, data);
    }
    // 
    async download(arg) {
        // 
        const duplicate = await deduplication.check(arg.id);
        if (duplicate) {
            return this.skip({
                id: arg.id,
                reason: 'duplicate',
            }, lang.transl('_', arg.id));
        }
        // 
        this.fileName = fileName.getFileName(arg.data);
        // 
        this.setProgressBar(0, 0);
        // 
        let url;
        if (arg.data.type === 3) {
            // 
            if (arg.data.novelMeta) {
                let blob = await MakeNovelFile.make(arg.data.novelMeta);
                url = URL.createObjectURL(blob);
            }
            else {
                throw new Error('Not found novelMeta');
            }
        }
        else {
            //  url url
            url = arg.data[settings.imageSize] || arg.data.original;
        }
        let xhr = new XMLHttpRequest();
        xhr.open('GET', url, true);
        xhr.responseType = 'blob';
        // 
        xhr.addEventListener('progress', async (event) => {
            // 
            if (!this.sizeChecked) {
                this.sizeChecked = true;
                const result = await filter.check({ size: event.total });
                if (!result) {
                    //  0 
                    this.setProgressBar(1, 1);
                    this.skip({
                        id: arg.id,
                        reason: 'size',
                    }, lang.transl('_', arg.id));
                }
            }
            if (this.cancel) {
                xhr.abort();
                xhr = null;
                return;
            }
            this.setProgressBar(event.loaded, event.total);
        });
        //  loadend
        xhr.addEventListener('loadend', async () => {
            if (this.cancel) {
                xhr = null;
                return;
            }
            let file = xhr.response; // 
            // 
            const downloadError = () => {
                let msg = '';
                if (xhr.status === 404) {
                    // 404 
                    msg = lang.transl('_file404', arg.id);
                }
                else {
                    // 
                    msg = lang.transl('_', arg.id);
                }
                // 
                // log.error(msg, 1)
                this.cancel = true;
                EVT.fire(EVT.list.downloadError, arg.id);
            };
            if (xhr.status !== 200) {
                // 
                //  200
                // 
                progressBar.showErrorColor(this.progressBarIndex, true);
                this.retry++;
                if (this.retry >= this.retryMax) {
                    //  retryMax 
                    // console.log(arg.data.id + ' retryMax')
                    downloadError();
                }
                else {
                    return this.download(arg);
                }
            }
            else {
                // 
                progressBar.showErrorColor(this.progressBarIndex, false);
                // 
                const convertExt = ['webm', 'gif', 'png'];
                const ext = settings.ugoiraSaveAs;
                if (convertExt.includes(ext) && arg.data.ugoiraInfo) {
                    try {
                        if (ext === 'webm') {
                            file = await converter.webm(file, arg.data.ugoiraInfo);
                        }
                        if (ext === 'gif') {
                            file = await converter.gif(file, arg.data.ugoiraInfo);
                        }
                        if (ext === 'png') {
                            file = await converter.apng(file, arg.data.ugoiraInfo);
                        }
                    }
                    catch (error) {
                        const msg = `Convert ugoira error, id ${arg.data.idNum}.`;
                        // 
                        // log.error(msg, 1)
                        console.error(msg);
                        this.cancel = true;
                        EVT.fire(EVT.list.downloadError, arg.id);
                    }
                }
            }
            if (this.cancel) {
                return;
            }
            // 
            const blobUrl = URL.createObjectURL(file);
            // 
            // 
            // 
            if (arg.data.type === 0 || arg.data.type === 1) {
                const result = await filter.check({
                    mini: blobUrl,
                });
                if (!result) {
                    return this.skip({
                        id: arg.id,
                        reason: 'color',
                    }, lang.transl('_', arg.id));
                }
            }
            // 
            // 
            if ((arg.data.type === 0 || arg.data.type === 1) &&
                !arg.data.id.includes('p0')) {
                const img = await DOM.loadImg(blobUrl);
                const result = await filter.check({
                    width: img.naturalWidth,
                    height: img.naturalHeight,
                });
                if (!result) {
                    return this.skip({
                        id: arg.id,
                        reason: 'widthHeight',
                    }, lang.transl('_', arg.id));
                }
            }
            // 
            this.browserDownload(blobUrl, this.fileName, arg.id, arg.taskBatch);
            xhr = null;
            file = null;
        });
        xhr.send();
    }
    // 
    browserDownload(blobUrl, fileName, id, taskBatch) {
        // 
        if (this.cancel) {
            //  bloburl
            URL.revokeObjectURL(blobUrl);
            return;
        }
        const sendData = {
            msg: 'send_download',
            fileUrl: blobUrl,
            fileName: fileName,
            id,
            taskBatch,
        };
        chrome.runtime.sendMessage(sendData);
    }
}
export { Download };
