import { EVT } from './EVT';
import { lang } from './Lang';
// 
class ShowConvertCount {
    constructor(el) {
        this.el = el;
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.convertChange, (ev) => {
            const count = ev.detail.data;
            let convertText = '';
            if (count > 0) {
                convertText = lang.transl('_', count.toString());
            }
            this.el.textContent = convertText;
        });
    }
}
export { ShowConvertCount };
