import { lang } from './Lang';
import { API } from './API';
import { log } from './Log';
import { DOM } from './DOM';
import { EVT } from './EVT';
// 
class SaveUserCover {
    constructor() {
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.saveUserCover, () => {
            this.saveUserCover();
        });
    }
    async saveUserCover() {
        const userId = DOM.getUserId();
        const userProfile = await API.getUserProfile(userId);
        const bgData = userProfile.body.background;
        if (bgData === null) {
            return EVT.sendMsg({
                msg: lang.transl('_'),
                type: 'error',
            });
        }
        const bgUrl = bgData.url;
        if (!bgUrl) {
            return EVT.sendMsg({
                msg: lang.transl('_'),
                type: 'error',
            });
        }
        // 
        const img = await fetch(bgUrl);
        const blob = await img.blob();
        // 
        const arr = bgUrl.split('.');
        const ext = arr[arr.length - 1];
        // 
        const url = URL.createObjectURL(blob);
        const name = `${userProfile.body.name}_${userId}_cover.${ext}`;
        DOM.downloadFile(url, name);
        log.success(' ' + lang.transl('_'));
        EVT.fire(EVT.list.closeCenterPanel);
    }
}
new SaveUserCover();
