// 
import { lang } from './Lang';
import { Colors } from './Colors';
import { DOM } from './DOM';
import { API } from './API';
import { store } from './Store';
import { log } from './Log';
import { EVT } from './EVT';
import { options } from './setting/Options';
import { settings } from './setting/Settings';
import { settingAPI } from './setting/SettingAPI';
import { states } from './States';
import { saveArtworkData } from './artwork/SaveArtworkData';
import { saveNovelData } from './novel/SaveNovelData';
import { mute } from './Mute';
import './SelectWork';
class InitPageBase {
    constructor() {
        this.crawlNumber = 0; // /
        this.maxCount = 1000; // /
        this.startpageNo = 1; //  api 
        this.listPageFinished = 0; // 
        this.ajaxThreadsDefault = 10; // 
        this.ajaxThreads = this.ajaxThreadsDefault; // 
        this.ajaxThreadsFinished = 0; //  ajaxThreads 
    }
    //  init 
    init() {
        options.showAllOption();
        this.setFormOption();
        this.addCrawlBtns();
        this.addAnyElement();
        this.initAny();
        if (!states.addEvented) {
            this.bindCommonEvent();
            states.addEvented = true;
        }
    }
    // 
    setFormOption() {
        // /
        options.setWantPageTip({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: lang.transl('_1'),
        });
    }
    // 
    addCrawlBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    // 
    addAnyElement() { }
    // 
    //  init 
    // 
    initAny() { }
    // 
    destroy() {
        DOM.clearSlot('crawlBtns');
        DOM.clearSlot('otherBtns');
    }
    bindCommonEvent() {
        window.addEventListener(EVT.list.pageSwitchedTypeChange, () => {
            this.destroy();
        });
        // 
        window.addEventListener(EVT.list.pageSwitch, () => {
            if (!states.busy) {
                EVT.fire(EVT.list.clearLog);
            }
        });
        //  id 
        window.addEventListener(EVT.list.downloadIdList, (ev) => {
            const idList = ev.detail.data;
            if (idList) {
                this.downloadIdList(idList);
            }
        });
    }
    // /
    getWantPageError() {
        EVT.fire(EVT.list.wrongSetting);
        const msg = lang.transl('_');
        EVT.sendMsg({
            msg: msg,
            type: 'error',
        });
        throw new Error(msg);
    }
    // /
    //  -1 0
    checkWantPageInput(crawlPartTip, crawlAllTip) {
        const temp = settings.setWantPage;
        //  1  -1
        if ((temp < 1 && temp !== -1) || isNaN(temp)) {
            //  1  -1 , 0 
            throw this.getWantPageError();
        }
        if (temp >= 1) {
            log.warning(crawlPartTip.replace('{}', temp.toString()));
        }
        else if (temp === -1) {
            log.warning(crawlAllTip);
        }
        return temp;
    }
    // /
    //  0
    //  max 
    //  page 
    checkWantPageInputGreater0(max, page) {
        if (settings.setWantPage > 0) {
            const want = settings.setWantPage > max ? max : settings.setWantPage;
            if (page) {
                log.warning(lang.transl('_x', want.toString()));
            }
            else {
                log.warning(lang.transl('_x', want.toString()));
            }
            return want;
        }
        else {
            throw this.getWantPageError();
        }
    }
    // 
    getWantPage() { }
    //  filter
    getMultipleSetting() {
        // 
        if (settings.firstFewImagesSwitch) {
            log.warning(lang.transl('_n', settingAPI.getFirstFewImages().toString()));
        }
    }
    // 
    async readyCrawl() {
        // 
        if (states.busy) {
            return EVT.sendMsg({
                msg: lang.transl('_2'),
                type: 'error',
            });
        }
        log.clear();
        log.success(lang.transl('_0'));
        await mute.getMuteSettings();
        this.getWantPage();
        this.getMultipleSetting();
        EVT.fire(EVT.list.crawlStart);
        // 
        this.nextStep();
    }
    //  id 
    //  id 
    //  id  getIdList 
    async downloadIdList(idList) {
        // 
        if (states.busy) {
            return EVT.sendMsg({
                msg: lang.transl('_2'),
                type: 'error',
            });
        }
        log.clear();
        log.success(lang.transl('_0'));
        await mute.getMuteSettings();
        this.getMultipleSetting();
        EVT.fire(EVT.list.crawlStart);
        store.idList = idList;
        this.getIdListFinished();
    }
    // 
    nextStep() {
        this.getIdList();
    }
    // id 
    getIdListFinished() {
        this.resetGetIdListStatus();
        EVT.fire(EVT.list.getIdListFinished);
        if (states.bookmarkMode) {
            return;
        }
        if (store.idList.length === 0) {
            return this.noResult();
        }
        log.log(lang.transl('_', store.idList.length.toString()));
        //  return 
        // return states.allWork = false
        if (store.idList.length <= this.ajaxThreadsDefault) {
            this.ajaxThreads = store.idList.length;
        }
        else {
            this.ajaxThreads = this.ajaxThreadsDefault;
        }
        for (let i = 0; i < this.ajaxThreads; i++) {
            this.getWorksData();
        }
    }
    // 
    async getWorksData(idData) {
        idData = idData || store.idList.shift();
        const id = idData.id;
        if (!id) {
            const msg = 'Error: work id is invalid!';
            EVT.sendMsg({
                msg: msg,
                type: 'error',
            });
            throw new Error(msg);
        }
        let failed = false; // 
        try {
            if (idData.type === 'novels') {
                const data = await API.getNovelData(id);
                await saveNovelData.save(data);
            }
            else {
                const data = await API.getArtworkData(id);
                await saveArtworkData.save(data);
            }
        }
        catch (error) {
            if (error.status) {
                // 
                this.logErrorStatus(error.status, id);
            }
            else {
                // 
                //  save 
                failed = true;
                console.error(error);
            }
        }
        if (failed) {
            // 
            setTimeout(() => {
                this.getWorksData(idData);
            }, 2000);
        }
        else {
            this.afterGetWorksData();
        }
    }
    // 
    afterGetWorksData() {
        this.logResultTotal();
        if (store.idList.length > 0) {
            // 
            this.getWorksData();
        }
        else {
            // 
            this.ajaxThreadsFinished++;
            if (this.ajaxThreadsFinished === this.ajaxThreads) {
                // 
                this.ajaxThreadsFinished = 0;
                this.crawlFinished();
            }
        }
    }
    // 
    crawlFinished() {
        if (store.result.length === 0) {
            return this.noResult();
        }
        store.crawlCompleteTime = new Date();
        this.sortResult();
        log.log(lang.transl('_n', store.result.length.toString()));
        log.log(lang.transl('_'), 2);
        // 
        EVT.fire(EVT.list.crawlFinish);
    }
    // 
    logErrorStatus(status, id) {
        switch (status) {
            case 0:
                log.error(id + ': ' + lang.transl('_0'));
                break;
            case 400:
                log.error(id + ': ' + lang.transl('_400'));
                break;
            case 403:
                log.error(id + ': ' + lang.transl('_403'));
                break;
            case 404:
                log.error(id + ': ' + lang.transl('_404'));
                break;
            default:
                log.error(lang.transl('_', id));
                break;
        }
    }
    // 
    logResultTotal() {
        log.log(lang.transl('_n', store.result.length.toString()), 1, false);
    }
    //  0 
    noResult() {
        //  crawlFinish crawlEmpty crawlEmpty 
        //  crawlFinish crawlEmpty 
        EVT.fire(EVT.list.crawlFinish);
        EVT.fire(EVT.list.crawlEmpty);
        const msg = lang.transl('_');
        log.error(msg, 2);
        EVT.sendMsg({
            msg: msg,
            type: 'error',
        });
    }
    // 
    sortResult() { }
}
export { InitPageBase };
