import { EVT } from '../EVT';
import { lang } from '../Lang';
import { log } from '../Log';
import { settings } from './Settings';
//  API
class SettingAPI {
    constructor() {
        this.firstFewImages = 0; // 
        this.bindEvents();
        this.firstFewImages = this.getFirstFewImages();
    }
    bindEvents() {
        //  firstFewImages 
        window.addEventListener(EVT.list.settingChange, () => {
            this.firstFewImages = this.getFirstFewImages();
        });
    }
    // 
    getFirstFewImages() {
        if (settings.firstFewImages > 0) {
            return settings.firstFewImages;
        }
        // 0
        EVT.fire(EVT.list.wrongSetting);
        const msg = lang.transl('_') + ' ' + lang.transl('_0');
        log.error(msg);
        EVT.sendMsg({
            msg: msg,
            type: 'error',
        });
        throw new Error(msg);
    }
    // 
    getDLCount(pageCount) {
        if (settings.firstFewImagesSwitch && this.firstFewImages <= pageCount) {
            return this.firstFewImages;
        }
        return pageCount;
    }
}
const settingAPI = new SettingAPI();
export { settingAPI };
