import { EVT } from './EVT';
import { settings } from './setting/Settings';
//  className
//  white  className  className
class Theme {
    constructor() {
        this.defaultTheme = 'white'; // 
        this.theme = ''; // 
        //  className
        this.classNameMap = new Map([
            ['white', ''],
            ['dark', 'theme-dark'],
        ]);
        this.selector = '#gtm-var-theme-kind'; // 
        this.timer = 0;
        // 
        this.htmlFlagMap = new Map([
            ['', 'white'],
            ['default', 'white'],
            ['dark', 'dark'],
        ]);
        this.elList = []; // 
        this.setTheme();
        this.bindEvents();
    }
    bindEvents() {
        // 
        this.timer = window.setInterval(() => {
            this.findFlag();
        }, 300);
        // 
        window.addEventListener(EVT.list.settingChange, () => {
            this.setTheme();
        });
    }
    //  pixiv 
    findFlag() {
        const el = document.querySelector(this.selector);
        if (el) {
            window.clearInterval(this.timer);
            this.setTheme();
            //  textContent 
            const ob = new MutationObserver((mutationsList) => {
                for (const item of mutationsList) {
                    if (item.type === 'characterData') {
                        this.setTheme();
                        break;
                    }
                }
            });
            ob.observe(el, {
                characterData: true,
                subtree: true,
            });
        }
    }
    //  pixiv 
    getThemeFromHtml() {
        const el = document.querySelector(this.selector);
        if (!el) {
            return this.defaultTheme;
        }
        return this.htmlFlagMap.get(el.textContent);
    }
    //  theme 
    setTheme() {
        let result = ''; // 
        // 
        switch (settings.theme) {
            case 'white':
                result = 'white';
                break;
            case 'dark':
                result = 'dark';
                break;
            default:
                //  'auto'
                result = this.getThemeFromHtml() || this.defaultTheme;
                break;
        }
        // 
        if (result !== this.theme) {
            this.theme = result;
            for (const el of this.elList) {
                this.setClass(el);
            }
        }
    }
    // 
    register(el) {
        this.elList.push(el);
        this.setClass(el);
    }
    //  className
    setClass(el) {
        //  className
        for (const className of this.classNameMap.values()) {
            if (el.classList.contains(className)) {
                el.classList.remove(className);
            }
        }
        //  className
        const name = this.classNameMap.get(this.theme);
        name && el.classList.add(name);
    }
}
const theme = new Theme();
export { theme };
