// /
import { InitPageBase } from '../InitPageBase';
import { Colors } from '../Colors';
import { API } from '../API';
import { lang } from '../Lang';
import { DOM } from '../DOM';
import { options } from '../setting/Options';
import { filter } from '../Filter';
import { store } from '../Store';
import { log } from '../Log';
class InitSeriesPage extends InitPageBase {
    constructor() {
        super();
        this.seriesId = '';
        this.init();
    }
    addCrawlBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')],
        ]).addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    initAny() { }
    setFormOption() {
        // /
        this.maxCount = 100;
        options.setWantPageTip({
            text: lang.transl('_'),
            tip: lang.transl('_'),
            rangTip: `1 - ${this.maxCount}`,
        });
    }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInputGreater0(this.maxCount, true);
    }
    nextStep() {
        // 
        const p = API.getURLSearchField(location.href, 'p');
        this.startpageNo = parseInt(p) || 1;
        //  id
        this.seriesId = API.getURLPathField('series');
        this.getIdList();
    }
    async getIdList() {
        let p = this.startpageNo + this.listPageFinished;
        const data = await API.getSeriesData(this.seriesId, p);
        this.listPageFinished++;
        //  id 
        const idList = [];
        for (const info of data.body.page.series) {
            idList.push(info.workId);
        }
        // data.body.page.series illust 
        //  illust  id 
        for (const work of data.body.thumbnails.illust) {
            if (!idList.includes(work.id)) {
                continue;
            }
            if (work.isAdContainer) {
                continue;
            }
            // 
            const filterOpt = {
                id: work.id,
                tags: work.tags,
                bookmarkData: !!work.bookmarkData,
                width: work.width,
                height: work.height,
                illustType: work.illustType,
                userId: work.userId,
                createDate: work.createDate,
            };
            //  api  illust  type  unknown
            if (await filter.check(filterOpt)) {
                store.idList.push({
                    type: 'unknown',
                    id: work.id,
                });
            }
        }
        //  data.body.page.series 
        const endFlag = data.body.page.series.length === 0;
        // 
        if (endFlag ||
            p >= this.maxCount ||
            this.listPageFinished === this.crawlNumber) {
            log.log(lang.transl('_'));
            this.getIdListFinished();
        }
        else {
            // 
            log.log(lang.transl('_', this.listPageFinished.toString()), 1, false);
            this.getIdList();
        }
    }
    resetGetIdListStatus() {
        this.listPageFinished = 0;
    }
}
export { InitSeriesPage };
