import { filter } from '../Filter';
import { store } from '../Store';
import { settings } from '../setting/Settings';
// 
class SaveNovelData {
    async save(data) {
        //  illustType   illustType  3
        const illustType = 3;
        // 
        const body = data.body;
        const bmk = body.bookmarkCount; // 
        const tagArr = body.tags.tags; //  tag 
        const tags = []; //  tag 
        // 
        for (const tagData of tagArr) {
            tags.push(tagData.tag);
        }
        const filterOpt = {
            createDate: body.createDate,
            id: body.id,
            illustType: illustType,
            tags: tags,
            bookmarkCount: bmk,
            bookmarkData: body.bookmarkData,
            userId: body.userId,
        };
        // 
        if (await filter.check(filterOpt)) {
            const id = body.id;
            const idNum = parseInt(id);
            const title = body.title;
            const userId = body.userId;
            const user = body.userName;
            const bookmarked = !!body.bookmarkData;
            // 
            const rankData = store.getRankList(id);
            const rank = rankData ? '#' + rankData : '';
            // 
            const seriesTitle = body.seriesNavData ? body.seriesNavData.title : '';
            const seriesOrder = body.seriesNavData
                ? '#' + body.seriesNavData.order
                : '';
            // 
            let meta = '';
            let metaArr = [];
            const pageUrl = `https://www.pixiv.net/novel/show.php?id=${id}`;
            const tagsA = [];
            for (const tag of tags) {
                tagsA.push('#' + tag);
            }
            metaArr.push(title, user, pageUrl, body.description, tagsA.join('\n'));
            meta = metaArr.join('\n\n') + '\n\n\n';
            // 
            store.addResult({
                id: id,
                idNum: idNum,
                thumb: body.coverUrl || undefined,
                dlCount: 1,
                title: title,
                tags: tags,
                tagsWithTransl: tags,
                tagsTranslOnly: tags,
                user: user,
                userId: userId,
                //  ext  novelSaveAs 
                ext: settings.novelSaveAs,
                bmk: bmk,
                bookmarked: bookmarked,
                date: body.createDate,
                type: illustType,
                rank: rank,
                seriesTitle: seriesTitle,
                seriesOrder: seriesOrder,
                viewCount: body.viewCount,
                likeCount: body.likeCount,
                commentCount: body.commentCount,
                novelMeta: {
                    id: body.id,
                    title: body.title,
                    content: this.replaceFlag(body.content),
                    description: body.description,
                    coverUrl: body.coverUrl,
                    createDate: body.createDate,
                    userName: body.userName,
                    meta: meta,
                },
            });
        }
    }
    // '[[jumpuri:>https://www.amazon.co.jp/dp/4758092486]]'
    // 
    // 'https://www.amazon.co.jp/dp/4758092486'
    replaceJumpuri(str) {
        let reg = /\[\[jumpuri:(.*?)>(.*?)\]\]/g;
        let temp;
        while ((temp = reg.exec(str))) {
            str = str.replace(temp[0], `${temp[1].trim()}${temp[2].trim()}`);
            reg.lastIndex = 0;
        }
        return str;
    }
    // > '[[rb: > Lilith]]'
    // 
    // 'Lilith'
    replaceRb(str) {
        let reg = /\[\[rb:(.*?)>(.*?)\]\]/g;
        let temp;
        while ((temp = reg.exec(str))) {
            str = str.replace(temp[0], `${temp[1].trim()}${temp[2].trim()}`);
            reg.lastIndex = 0;
        }
        return str;
    }
    // > '[chapter:]'
    // 
    // ''
    replaceChapter(str) {
        const reg = /\[chapter:(.*?)\]/g;
        let temp;
        while ((temp = reg.exec(str))) {
            str = str.replace(temp[0], temp[1]);
            reg.lastIndex = 0;
        }
        return str;
    }
    // > [pixivimage:70551567]
    // 
    // [pixiv image link: <a href="http://pixiv.net/i/70551567" target="_blank">http://pixiv.net/i/70551567</a>]
    replacePixivImage(str) {
        let reg = /\[pixivimage:(.*?)\]/g;
        let temp;
        while ((temp = reg.exec(str))) {
            const url = `http://pixiv.net/i/${temp[1].trim()}`;
            str = str.replace(temp[0], `[pixiv image link: <a href="${url}" target="_blank">${url}</a>]`);
            reg.lastIndex = 0;
        }
        return str;
    }
    // 
    replaceFlag(str) {
        str = str.replace(/\[newpage\]/g, '');
        str = this.replaceJumpuri(str);
        str = str.replace(/\[jump:.*?\]/g, '');
        str = this.replaceRb(str);
        str = this.replaceChapter(str);
        str = this.replacePixivImage(str);
        return str;
    }
}
const saveNovelData = new SaveNovelData();
export { saveNovelData };
